'use strict';

var getReviewElement = require('./review.js');
var getCallback = require('./load.js');
var callbackName = 'JSONPCallback';
var CALLBACK_URL = 'http://localhost:1506/api/reviews?callback=';
var reviews = [];
var reviewsFilter = document.querySelector('.reviews-filter');

reviewsFilter.classList.add('invisible');

function renderToPage(data) {
  reviews = data;
  var reviewsList = document.querySelector('.reviews-list');
  console.log(reviews);

  reviews.forEach(function(review) {
    getReviewElement(review, reviewsList);
  });
  reviewsFilter.classList.remove('invisible');
}

getCallback(callbackName, CALLBACK_URL, renderToPage);



